<template>
  <div id="container"></div>
</template>

<script>
import AMapLoader from "@amap/amap-jsapi-loader";

export default {
  name: "map-view",
  mounted() {
    this.initAMap();
  },
  unmounted() {
    this.map?.destroy();
  },
  methods: {
    initAMap() {
      AMapLoader.load({
        key: "3c9f69fcb32ce219c2cb0ded9033a7c7", // WebKey load 
        version: "2.0", //  JSAPI  1.4.15
        plugins: ['AMap.Geolocation'], //  Geolocation 
      })
        .then((AMap) => {
          this.map = new AMap.Map("container", {
            viewMode: "3D", // 3D
            zoom: 15, // 
            center: [114.364372, 30.534545], // 
            resizeEnable: true, // 
          });

          // 
          this.map.plugin('AMap.Geolocation', () => {
            const geolocation = new AMap.Geolocation({
              enableHighAccuracy: true, // true
              timeout: 10000, // 
              maximumAge: 0, // 00
              convert: true, // true
              showMarker: true, // true
              showCircle: true, // true
              panToLocation: false, // true
              zoomToAccuracy: false // false
            });
            this.map.addControl(geolocation);
            geolocation.getCurrentPosition();
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
};
</script>

<style scoped>
#container {
  width: 100%;
  height: 80vh; /* 80% */
  max-width: 100%;
  max-height: 100%;
}
</style>
