import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './assets/css/blue-skin5152.css'
import './assets/css/bootstrap5152.css'
import './assets/css/green-skin5152.css'
import './assets/css/main5152.css'
import './assets/css/red-skin5152.css'
import './assets/css/responsive5152.css'
import './plugins/element.js'
import ElementUI from 'element-ui'
import './assets/css/ali.css'

//ecahrs
import ECharts from 'vue-echarts'
import 'echarts/lib/chart/bar'

Vue.component('chart', ECharts)
    //vuex
import Vuex from 'vuex'
import store from './store'
Vue.use(Vuex)
    //
import moment from 'moment'
//main.js //
Vue.directive('title', {
        //
        inserted: function(el, binding) {
            const { value } = binding
            if (el.dataset.title) {
                // 1data-title
                document.title = el.dataset.title
            } else if (value) {
                // 2
                if (typeof value === 'string') {
                    document.title = value
                } else if (typeof value === 'object' && value && value.title) {
                    document.title = value.title
                }
            }
        },
        update(el, binding, vnode, oldVnode) {
            const { value } = binding
            if (el.dataset.title) {
                document.title = el.dataset.title
            } else if (value) {
                // 2
                if (typeof value === 'string') {
                    document.title = value
                } else if (typeof value === 'object' && value && value.title) {
                    document.title = value.title
                }
            }
        }
    })
    //
Vue.filter('dataFormat', function(datastr, pattern = 'YYYY-MM-DD HH:mm') {
    return moment(datastr).format(pattern)
})
Vue.config.productionTip = false
    // aiox
import axios from 'axios'
import ipconfig from '../../ipconfig.js'
// axios.defaults.withCredentials = true
//acioxurl
axios.defaults.baseURL = 'http://'+ ipconfig.localIP +':3000'

//axiosvue
Vue.prototype.$axios = axios
    //post
axios.defaults.headers.post['Content-Type'] =
    'application/x-www-form-urlencoded; charset=UTF-8'
    //vue2axiosjsonquerystring
import qs from 'qs'
Vue.prototype.qs = qs

// 
// http request 
axios.interceptors.request.use(
    config => {
        let luffy_jwt_token = window.localStorage.getItem('luffy_jwt_token')
            // console.log(luffy_jwt_token)
        config.headers.Authorization = `Bearer ${luffy_jwt_token}`
        return config
    },
    err => {
        return Promise.reject(err)
    }
)

// 
axios.interceptors.response.use(
    function(response) {
        if (response.data && response.data.code) {
            if (parseInt(response.data.code) === 401) {
                //
                ElementUI.Notification({
                    title: '',
                    message: '',
                    type: 'warning'
                })
                store.dispatch('user/close') //
                store.dispatch('user/deleteuserinfo') //
                    // routerIndex.push('/login');
            }
            if (parseInt(response.data.code) === -1) {
                ElementUI.Notification({
                    title: '',
                    message: '',
                    type: 'warning'
                })
            }
        }
        return response
    },
    function(error) {
        ElementUI.Notification({
            title: '',
            message: '',
            type: 'warning'
        })
        return Promise.reject(error)
    }
)

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')