      </div>
      <div class="form-group">
        <label> - :</label>
        <input v-model.number="newCourse.startWeek" type="number" min="1" max="18" placeholder="" required>
        <input v-model.number="newCourse.endWeek" type="number" min="1" max="18" placeholder="" required>
      </div>
      <div class="form-group">
        <label> - :</label>
        <input v-model.number="newCourse.startPeriod" type="number" min="1" max="13" placeholder="" required>
        <input v-model.number="newCourse.endPeriod" type="number" min="1" max="13" placeholder="" required>
      </div>
      <div class="form-group">
        <label>:</label>
        <select v-model.number="newCourse.day" required>
          <option value="0"></option>
          <option value="1"></option>
          <option value="2"></option>
